  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

.header {
  width: 100%;
  z-index: 10;
  background-color: var(--primary-background-color);
  position: fixed;
}

.header-navigation {
  align-items: center;
  display: flex;
}

.logo {
  align-items: center;
  gap: 4px;
  padding: 24px 0 32px;
  display: flex;
}

.logo-text {
  letter-spacing: -.03em;
  text-transform: uppercase;
  font-style: italic;
  font-weight: 700;
}

.logo-decor {
  font-style: normal;
}

.header-navigation__list {
  font-weight: 500;
  display: none;
}

.header-navigation__item:hover .header-navigation__link, .header-navigation__item:focus .header-navigation__link {
  color: var(--accent-color);
}

.header-navigation__link {
  transition: color var(--duration) var(--time-function);
  padding: 15px 0;
  font-weight: 500;
  line-height: 1.14;
  display: inline-block;
  position: relative;
}

.header-navigation__link:after {
  content: "";
  width: 100%;
  height: 2px;
  background-color: var(--accent-color);
  transform-origin: 0;
  transition: transform var(--duration) var(--time-function);
  display: block;
  position: absolute;
  bottom: 3px;
  transform: scaleX(0);
}

.header-navigation__link:hover:after {
  transform: scaleX(100%);
}

.header-social__list {
  align-items: center;
  gap: 20px;
  display: none;
}

.header-social__item:hover .header-social__icon, .header-social__item:focus .header-social__icon {
  fill: var(--accent-color);
  transform: rotate(360deg);
}

.header-social__icon {
  fill: var(--primary-text-color);
  transition: fill 1s var(--time-function), transform 1s var(--time-function);
}

.js-open-menu {
  background-color: rgba(0, 0, 0, 0);
  border: none;
  margin-left: auto;
}

.js-menu-container {
  z-index: 100;
  width: 220px;
  max-height: 812px;
  background-color: var(--secondary-background-color);
  transition: transform 800ms var(--time-function);
  position: fixed;
  top: 0;
  bottom: 0;
  right: 0;
  transform: translateX(100%);
}

.js-menu-container.is-open {
  transform: translateX(0);
}

.mobile-menu {
  padding: 136px 30px 23px 50px;
}

.js-close-menu {
  width: 28px;
  height: 28px;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  position: absolute;
  top: 20px;
  right: 20px;
}

.modal-icon {
  fill: var(--white-color);
  transition: fill var(--duration) var(--time-function);
}

.js-close-menu:hover .modal-icon, .js-close-menu:focus .modal-icon {
  fill: var(--accent-color);
}

.mobile-menu .header-navigation__list {
  color: var(--white-color);
  flex-direction: column;
  gap: 9px;
  display: flex;
}

.mobile-menu .header-social__list {
  display: flex;
  position: absolute;
  bottom: 24px;
  left: 32px;
}

.mobile-menu .header-social__icon {
  fill: var(--white-color);
}

@media screen and (min-width: 768px) {
  .js-menu-container {
    width: 290px;
    max-height: 1024px;
  }

  .mobile-menu {
    padding: 156px 40px 23px 85px;
  }

  .js-close-menu {
    width: 32px;
    height: 32px;
    top: 24px;
    right: 32px;
  }
}

@media screen and (min-width: 1200px) {
  .logo-text {
    font-size: 18px;
    line-height: 1.11;
  }

  .header-navigation__list {
    align-items: center;
    gap: 24px;
    margin-left: auto;
    margin-right: 137px;
    display: flex;
  }

  .header-social__list {
    display: flex;
  }

  .js-open-menu, .js-menu-container {
    display: none;
  }
}

:root {
  --primary-font-family: "Montserrat", sans-serif;
  --secondary-font-family: "Roboto", sans-serif;
  --primary-text-color: #111;
  --primary-background-color: #fafafa;
  --secondary-background-color: #1e1823;
  --accent-color: #fd9222;
  --white-color: #fff;
  --duration: 250ms;
  --time-function: cubic-bezier(.4, 0, .2, 1);
}

body {
  font-family: var(--primary-font-family);
  letter-spacing: -.02em;
  color: var(--primary-text-color);
  background-color: var(--primary-background-color);
  font-size: 14px;
  font-weight: 400;
  line-height: 1.29;
}

.container {
  padding: 0 10px;
}

.section {
  padding-top: 72px;
}

.title {
  letter-spacing: -.03em;
  text-align: center;
  text-transform: uppercase;
  margin-bottom: 32px;
  font-size: 20px;
  font-weight: 600;
  line-height: 1.2;
}

.title-decor {
  color: var(--accent-color);
}

.primary-button {
  color: var(--white-color);
  background-color: var(--accent-color);
  transition: color var(--duration) var(--time-function), background var(--duration) var(--time-function);
  border: none;
  border-radius: 100px;
  font-weight: 600;
}

.primary-button:hover, .primary-button:focus {
  color: var(--accent-color);
  background: #fcf1e0;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

